@import '../../../node_modules/slick-carousel/slick/slick.scss';
//  @import '../../../node_modules/slick-carousel/slick/slick-theme.scss';

.carousel_item {
  display: flex;
}
// Buttons

.slick-arrow {
  position: absolute;
  top: 50%;

  border: none;
  background-color: transparent;
  padding: 0;
  cursor: pointer;
  color: var(--primary-color);

  font-size: 0;

  transition: color var(--animation);

  //   
  &::before {
    display: table;
    font-size: 27px;
  }

  &:hover,
  &:focus {
    color: var(--secondary-color);
  }
}

.slick-next {
  right: 0;

  &::before {
    content: '\276F';
  }
}
.slick-prev {
  left: 0;
  z-index: 9;

  &::before {
    content: '\276E';
  }
}

.slick-disabled {
  visibility: hidden;
}

// 
.slick-dots {
  @extend %list;

  display: flex;
  justify-content: center;
  height: 12px;
  align-items: flex-end;

  & li + li {
    margin-left: 18px;
  }

  button {
    padding: 0;
    border: none;
    background-color: var(--dots-gray-bk-cl);
    cursor: pointer;

    border-radius: 50%;
    width: 12px;
    height: 12px;

    font-size: 0;

    transition: all var(--animation);

    &:hover,
    &:focus {
      background-image: url(../images/reviews/home-btn.svg);
      background-size: contain;
      background-repeat: no-repeat;
      background-color: transparent;
    }
  }
}
.slick-active {
  button {
    background-image: url(../images/reviews/home-btn.svg);
    background-size: contain;
    background-repeat: no-repeat;
    background-color: transparent;
  }
}

